<template>
  <div>
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="240"></el-table-column>
      <!--            <el-table-column prop="parent.name" label=""></el-table-column>-->
      <el-table-column prop="title" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="$router.push(`/races/edit/${scope.row._id}`)"></el-button>
          <el-button type="text" size="small" @click="remove(scope.row)"></el-button><!-- scope.row -->
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: "RaceList",
    data(){
      return{
        items:[ ]
      }
    },
    created(){
      this.fetch()
    },
    methods: {
      async fetch() {
        const res = await this.$http.get('rest/races')
        this.items = res.data
      },


      async remove(row){
        this.$confirm(`"${row.title}"`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          const res=await this.$http.delete(`rest/races/${row._id}`)
          this.$message({
            type: 'success',
            message: '!'
          })
          this.fetch()
        })
      }
    }
  }
</script>

